* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

main img {
    position: absolute;
    z-index: -1;
    filter: brightness(150%);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 100%;
}

main {
    display: flex;
    flex-flow: column nowrap;
    row-gap: 0.5rem;
    align-items: center;
    min-height: 100%;
    color: black;
}

h1 {
    padding: 0.7em;
    color: snow;
}

form {
    display: flex;
    column-gap: 30px;
}

form input {
    display: block;
    border-radius: 10px;
    margin-left: 1em;
    padding-left: 1em;
    height: 4.7vh;
    width: 55%;
}

form button {
    width: 4rem;
    border-radius: 4em;
    background-color: #D3CDD9;
}

ul {
    padding-top: 1.5em;
    width: 80%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    row-gap: 2rem;
}

ul li {
    display: flex;
    width: 70%;
    font-size: 1.1rem;
    font-weight: bold;
    justify-content: space-between;
}

li input,
li button {
    width: 12%;
    border-radius: 20%;
}

li button {
    height: 5vh;
    width: 18%;
}

form button:hover,
li:hover {
    transform: scale(120%, 120%);
}